












export default {
	data() {
		return {
			userDTO: {
				mobile: '',
				password: '',
				password2:''
			}
		};
	},
	onLoad: function(option) {
		//option为object类型，会序列化上个页面传递的参数
		console.log(option.mobile);
		this.mobile = option.mobile;
	},
	methods: {
		signUp: function(userDTO) {
			if(this.password===this.password2){
				var _this = this;
				uni.request({
					url: this.apiServer + '/user/sign_up',
					method: 'POST',
					header: { 'content-type': 'application/json' },
					data: {
						mobile: _this.mobile,
						password: _this.password
					},
					success: res => {
					  	if (res.data.code === 0) {
				        uni.showModal({
							title: '提示',
							content: '注册成功,是否立即登录？',
							success:function(res){
								if(res.confirm){
									uni.navigateTo({
										url: '../signin/signin'
									});
								}
							}
						});
// 						uni.navigateTo({
// 							url: '../signin/signin'
// 							});
						} else {
							uni.showModal({
								title: '提示',
								content: res.data.msg
							});
						}
					}
				});
			}else{
				uni.showModal({
					title:'提示',
					content:'两次密码不一致！'
				});
			}
			
		}
	}
};
